
























































































































































import PageBase from '../../utils/page_base.utils';
import { Component, Ref, Vue } from 'vue-property-decorator';
import Menu from '../../components/Menu.vue';
import pdf from 'vue-pdf';
import Driver from 'driver.js';

@Component({
	components: { Menu, pdf },
})
export default class AppBase extends PageBase {
	private error: boolean = false;
	private isOpenSidebar: boolean = true;
	private isReduceSidebar: boolean = true;
	private isOpenModal: boolean = false;
	private isOpenInfoModal: boolean = false;
	private isOpenHelpModal: boolean = false;

	private isOpenCollapse = -1;

	private numPages: number = 0;
	private pdf_src: any = '';

	@Ref('child_page') child_page!: Vue & {
		moveNext: () => void;
		open_deposit: () => Promise<void>;
		open_withdrawal: () => Promise<void>;
		DepositStep: number;
		WithdrawalStep: number;
	};

	private moveNext: () => void = () => {
		0;
	};

	public async created() {
		await super.created();
		this.$i18n.locale = this.store.api.country.locale || 'en';
		this.statusSidebar();
		window.addEventListener('resize', this.statusSidebar);
		this.reload();
		this.$watch('$route', () => {
			this.statusSidebar();
		});
		this.$watch('isOpenInfoModal', async () => {
			this.pdf_src = await pdf.createLoadingTask(this.publicPath + 'doc5_' + this.$i18n.locale + '.pdf');
			this.pdf_src.promise.then((pdf: { numPages: number }) => {
				this.numPages = pdf.numPages;
			});
		});
	}

	private statusSidebar() {
		const w = window.innerWidth;
		if (w <= 768) {
			this.isOpenModal = false;
			this.isOpenSidebar = false;
			this.isReduceSidebar = true;
		} else if (w >= 769 && w <= 1023) {
			this.isOpenModal = false;
			this.isOpenSidebar = true;
			this.isReduceSidebar = true;
		} else {
			this.isOpenModal = false;
			this.isOpenSidebar = true;
			this.isReduceSidebar = true;
		}
	}

	private async triggerSidebar() {
		const w = window.innerWidth;
		if (w <= 768) {
			this.isOpenModal = !this.isOpenModal;
		} else if (w >= 769) {
			this.isReduceSidebar = !this.isReduceSidebar;
		}
		if (this.moveNext) {
			await this.sleep(100);
			this.moveNext();
		}
	}

	private get_helper_texts_i18n(option: string) {
		return [...Array(parseInt(this.L('help-center.' + option + '.text_count')))].map((_, i) =>
			this.L('help-center.' + option + '.texts.' + i),
		);
	}

	private async driver_gruide(option: string) {
		this.isOpenHelpModal = false;
		await this.sleep(100);
		switch (option) {
			case 'a':
				return this.drive_guide_a();
			case 'b':
				return this.drive_guide_b();
			case 'c':
				return this.drive_guide_c();
			default:
				break;
		}
	}

	private async drive_guide_a() {
		try {
			await this.$router.push({ name: 'Home' });
		} catch (_) {
			_;
		}
		const options = {
			doneBtnText: this.L('helper.done'),
			closeBtnText: this.L('helper.close'),
			nextBtnText: this.L('helper.next'),
			prevBtnText: this.L('helper.prev'),
			onReset: () => {
				this.child_page.moveNext = () => {
					0;
				};
			},
		};
		const driver = new Driver(options);
		const L = (step: number, key: number) => this.L(`driver-guide.a.${step}.${key === 1 ? 'title' : 'subtitle'}`);
		driver.defineSteps([
			{
				element: '#driver-guide-a-1',
				popover: {
					title: L(1, 1),
					description: L(1, 2),
					position: 'left',
				},
				onNext: async () => {
					driver.preventMove();
					await this.child_page.open_deposit();
				},
			},
			{
				element: '#driver-guide-a-2',
				popover: {
					title: L(2, 1),
					description: L(2, 2),
					position: 'top',
				},
			},
		]);
		this.child_page.moveNext = () => {
			driver.defineSteps([
				{
					element: '#driver-guide-a-2',
					stageBackground: '#ffffff00',
					popover: {
						title: L(2, 1),
						description: L(2, 2),
						position: 'top',
					},
					onNext: () => {
						driver.preventMove();
						(driver as any).overlay.options = {
							...(driver as any).overlay.options,
							onReset: () => {
								0;
							},
						};
						driver.reset();
						this.child_page.moveNext = async () => {
							await this.sleep(100);
							(driver as any).overlay.options = {
								...(driver as any).overlay.options,
								onReset: () => {
									this.child_page.moveNext = () => {
										0;
									};
								},
							};
							driver.defineSteps([
								{
									element: '#driver-guide-a-3',
									stageBackground: '#ffffff00',
									popover: {
										title: L(3, 1),
										description: L(3, 2),
										position: 'top',
									},
									onNext: async () => {
										driver.preventMove();
										(driver as any).overlay.options = {
											...(driver as any).overlay.options,
											onReset: () => {
												0;
											},
										};
										driver.reset();
										this.child_page.DepositStep = 1;
										await this.sleep(100);
										this.child_page.moveNext = () => {
											driver.defineSteps([
												{
													element: '#driver-guide-a-4',
													stageBackground: '#ffffff00',
													popover: {
														title: L(4, 1),
														description: L(4, 2),
														position: 'top',
													},
												},
												{
													element: '#driver-guide-a-4-end',
												},
											]);
											this.child_page.moveNext = async () => {
												await this.sleep(100);
												driver.defineSteps([
													{
														element: '#driver-guide-a-3',
														stageBackground: '#ffffff00',
														popover: {
															title: L(5, 1),
															description: L(5, 2),
															position: 'top',
														},
														onNext: async () => {
															driver.preventMove();
															(driver as any).overlay.options = {
																...(driver as any).overlay.options,
																onReset: () => {
																	0;
																},
															};
															driver.reset();
															this.child_page.DepositStep = 2;
															await this.sleep(100);
															this.child_page.moveNext = () => {
																driver.defineSteps([
																	{
																		element: '#driver-guide-a-6',
																		stageBackground: '#ffffff00',
																		popover: {
																			title: L(6, 1),
																			description: L(6, 2),
																			position: 'top',
																		},
																	},
																]);
																driver.start();
															};
															this.child_page.moveNext();
														},
													},
													{
														element: '#driver-guide-a-3-end',
													},
												]);
												driver.start();
											};
											driver.start();
										};
										this.child_page.moveNext();
									},
								},
								{
									element: '#driver-guide-a-3-end',
								},
							]);
							driver.start();
						};
					},
				},
				{
					element: '#driver-guide-a-2-end',
				},
			]);
			driver.start();
		};
		driver.start();
	}

	private async drive_guide_b() {
		try {
			await this.$router.push({ name: 'Home' });
		} catch (_) {
			_;
		}
		const options = {
			doneBtnText: this.L('helper.done'),
			closeBtnText: this.L('helper.close'),
			nextBtnText: this.L('helper.next'),
			prevBtnText: this.L('helper.prev'),
			onReset: () => {
				this.child_page.moveNext = () => {
					0;
				};
			},
		};
		const driver = new Driver(options);
		const L = (step: number, key: number) => this.L(`driver-guide.b.${step}.${key === 1 ? 'title' : 'subtitle'}`);
		driver.defineSteps([
			{
				element: '#driver-guide-b-1',
				popover: {
					title: L(1, 1),
					description: L(1, 2),
					position: 'left',
				},
				onNext: async () => {
					driver.preventMove();
					await this.child_page.open_withdrawal();
				},
			},
			{
				element: '#driver-guide-b-1-end',
			},
		]);
		this.child_page.moveNext = () => {
			driver.defineSteps([
				{
					element: '#driver-guide-b-2',
					stageBackground: '#ffffff00',
					popover: {
						title: L(2, 1),
						description: L(2, 2),
						position: 'top',
					},
					onNext: () => {
						driver.preventMove();
						(driver as any).overlay.options = {
							...(driver as any).overlay.options,
							onReset: () => {
								0;
							},
						};
						driver.reset();
						this.child_page.moveNext = async () => {
							await this.sleep(100);
							(driver as any).overlay.options = {
								...(driver as any).overlay.options,
								onReset: () => {
									this.child_page.moveNext = () => {
										0;
									};
								},
							};
							driver.defineSteps([
								{
									element: '#driver-guide-b-3',
									stageBackground: '#ffffff00',
									popover: {
										title: L(3, 1),
										description: L(3, 2),
										position: 'top',
									},
									onNext: async () => {
										driver.preventMove();
										(driver as any).overlay.options = {
											...(driver as any).overlay.options,
											onReset: () => {
												0;
											},
										};
										driver.reset();
										this.child_page.WithdrawalStep = 1;
										await this.sleep(100);
										this.child_page.moveNext = () => {
											driver.defineSteps([
												{
													element: '#driver-guide-b-4',
													stageBackground: '#ffffff00',
													popover: {
														title: L(4, 1),
														description: L(4, 2),
														position: 'top',
													},
												},
											]);
											this.child_page.moveNext = () => {
												0;
											};
											driver.start();
										};
										this.child_page.moveNext();
									},
								},
								{
									element: '#driver-guide-a-3-end',
								},
							]);
							driver.start();
						};
					},
				},
				{
					element: '#driver-guide-a-2-end',
				},
			]);
			driver.start();
		};
		driver.start();
	}

	private drive_guide_c() {
		const driver = new Driver({
			doneBtnText: this.L('helper.done'),
			closeBtnText: this.L('helper.close'),
			nextBtnText: this.L('helper.next'),
			prevBtnText: this.L('helper.prev'),
		});
		const L = (step: number, key: number) => this.L(`driver-guide.c.${step}.${key === 1 ? 'title' : 'subtitle'}`);
		this.moveNext = driver.moveNext;
		driver.defineSteps([
			{
				element: '#driver-guide-c-1',
				popover: {
					title: L(1, 1),
					description: L(1, 2),
					position: 'left',
				},
				onNext: async () => {
					driver.preventMove();
					await this.triggerSidebar();
				},
			},
			{
				element: '#driver-guide-c-2',
				popover: {
					title: L(2, 1),
					description: L(2, 2),
					position: 'right',
				},
				onNext: async () => {
					driver.preventMove();
					await this.$router
						.push({ name: 'Setting' })
						.then(async () => {
							await this.sleep(250);
							driver.highlight({
								element: '#driver-guide-c-3',
								popover: {
									title: L(3, 1),
									description: L(3, 2),
									position: 'top',
								},
							});
						})
						.catch(() => {
							driver.moveNext();
						});
				},
			},
			{
				element: '#driver-guide-c-3',
				popover: {
					title: L(3, 1),
					description: L(3, 2),
					position: 'top',
				},
			},
		]);
		driver.start();
	}
}
